.comment {
  margin-bottom: 10px;
  display: flex;
  .firstLevelComment {
    :first-child {
      font-size: 8pt;
      margin-top: 2px;
      margin-bottom: 5px;
      color: var(--ligth-gray);
      span {
        &:hover {
          text-decoration: underline;
        }
        cursor: pointer;
      }
    }
  }
  a {
    text-decoration: underline;
  }
  p {
    margin-top: 5px;
  }
  img {
    width: 10px;
    height: 10px;
    margin-top: 1px;
    margin-right: 5px;
  }
  .reply {
  }
}
