* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Verdana, Gavena, sans-serif;
  font-size: 10pt;
}
.root {
  display: grid;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  margin: 8px;
}

.App {
  width: 85%;
  min-width: 796px;
  min-height: 100vh;
  margin: 0 auto;
}

:root {
  --black: #000000;
  --white: #ffffff;
  --gray-yellow: rgb(246, 246, 239);
  --ligth-gray: #828282;
  --orange: hsl(24deg 100% 50%);
}
