.newsItem {
  color: var(--black);
  margin-bottom: 5px;
  div {
    color: var(--ligth-gray);
    display: flex;
    text-align: start;
    align-self: start;
    align-items: start;
    align-content: start;

    span,
    a {
      font-size: 7pt;
      :hover {
        text-decoration: underline;
      }
    }
  }
